<template>
  <div class="match-box">
    <div v-show="ifShow" class="layer"></div>
    <div v-show="ifShow" class="box-content">
      <ContentBox
        class="box"
        :data="data"
        :openid="openid"
        :flag="ifShow"
        :likeFlag="1"
      />
      <img v-on:click="close" class="close" src="../assets/关  闭.svg" />
    </div>
    <div class="actor-content" v-on:click="show">
      <img class="img" :src="data.imgUrl[0]" />
      <p class="nick-name">{{ data.nickName }}</p>
    </div>
  </div>
</template>

<script>
import ContentBox from "../components/ContentBox";

export default {
  name: "MatchBox",
  props: ["data", "openid"],
  components: {
    ContentBox,
  },
  data: function () {
    return {
      ifShow: 0,
    };
  },
  methods: {
    show: function () {
      this.$data.ifShow = 1;
    },
    close: function () {
      this.$data.ifShow = 0;
    },
  },
};
</script>

<style scoped>
.match-box {
  margin-bottom: 20px;
  width: 100px;
  color: rgb(255, 118, 141);
}
.actor-content {
  width: 90px;
  height: 130px;
  border-style: solid;
  border-radius: 10px;
  border-width: 2px;
  border-color: rgb(255, 118, 141);
}
.img {
  width: 80px;
}
.layer {
  z-index: 150;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.6;
}
.box {
  z-index: 200;
  background-color: white;
  position: fixed;
  width: 93%;
  max-width: 500px;
  left: 0;
  top: 30px;
}
.close {
  z-index: 200;
  position: absolute;
  right: 10px;
  top: 35px;
  width: 35px;
  height: 35px;
  z-index: 200;
}
</style>
